/*     */
.dark {
  .categories-list-name {
    color: rgba(255, 255, 255, 0.6);

    &:hover,
    :focus {
      color: $select-el-color;
    }
  }
  .aside-link {
    color: rgba(255, 255, 255, 0.6);

    &:hover,
    :focus {
      color: $select-el-color;
    }
  }
  .aside-list::-webkit-scrollbar-track {
    background: $dark-body-bg-color;
  }
  .aside-list::-webkit-scrollbar-thumb {
    background-color: $dark-color;
  }
  .books-section-title {
    color: $light-color;
  }
  .cards-title {
    color: $light-color;
  }
  .cards-author {
    color: $secondery-text-color;
  }
  .activ-name {
    color: $select-el-color;
  }
}

/* Categories */
.categories-list {
  width: 100%;
  height: 228px;
  overflow: hidden;

  @media screen and (min-width: 768px) {
    width: 337px;
    height: 445px;
  }
}

.categories-list-name {
  display: block;
  margin-bottom: 24px;
  color: rgba(17, 17, 17, 0.6);
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: -0.32px;
  transform: color $anim-tranz-parameters;

  &:hover,
  :focus {
    color: $accent-color;
  }

  @media screen and (min-width: 768px) {
    margin-bottom: 32px;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -0.36px;
  }
}

.aside {
  width: 337px;

  @media screen and (min-width: 768px) {
    width: 356px;
  }
}

.aside-list {
  display: flex;
  flex-direction: column;
  gap: 24px;
  height: 186px;
  overflow-y: scroll;

  @media screen and (min-width: 768px) {
    gap: 32px;
    height: 600px;
  }

  @media screen and (min-width: 1440px) {
    height: 416px;
  }
}

.aside-link {
  color: $categories-text-color;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: -0.32px;
  transition: color $anim-tranz-parameters;

  &:hover,
  :focus {
    color: $accent-color;
  }

  @media screen and (min-width: 768px) {
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -0.36px;
  }
}

.activ-name {
  color: $accent-color;
  font-weight: 700;
  text-transform: uppercase;
}

.aside-list {
  scrollbar-width: thin;
}

/*   Chrome, Edge  Safari */
.aside-list::-webkit-scrollbar {
  width: 8px;
}

.aside-list::-webkit-scrollbar-track {
  background: $light-color;
}

.aside-list::-webkit-scrollbar-thumb {
  background-color: #e0e0e0;
  border-radius: 12px;
}

/* Books */
.books-section-title {
  margin-bottom: 40px;
  text-align: left;
  color: $dark-color;
  font-family: DM Sans;
  font-size: 32px;
  font-style: normal;
  font-weight: 700;
  line-height: 38px;
  letter-spacing: -1.28px;
}

.card-list {
  display: flex;
  flex-direction: column;

  @media screen and (min-width: 768px) {
    flex-wrap: wrap;
    flex-direction: row;
  }

  @media screen and (min-width: 1440px) {
  }
}

.cards-item {
  margin-bottom: 40px;

  &:nth-last-child(-n + 1) {
    margin-bottom: 0;
  }

  @media screen and (min-width: 768px) {
    width: 218px;
    margin-right: 25px;

    &:nth-child(3n) {
      margin-right: 0;
    }

    &:nth-last-child(-n + 3) {
      margin-bottom: 0;
    }
  }

  @media screen and (min-width: 1440px) {
    width: 170px;
    margin-right: 24px;

    &:nth-child(3n) {
      margin-right: 24px;
    }

    &:nth-child(5n) {
      margin-right: 0;
    }

    &:nth-last-child(-n + 3) {
      margin-bottom: 40px;
    }

    &:nth-last-child(-n + 5) {
      margin-bottom: 0;
    }
  }
}

.cards-link {
  cursor: default;
}

.cards-header {
  margin-bottom: 14px;
  border-radius: 8px;
  overflow: hidden;
}

.cards-img {
  width: 100%;
  height: 485px;
  cursor: pointer;

  @media screen and (min-width: 768px) {
    height: 316px;
  }

  @media screen and (min-width: 1440px) {
    height: 256px;
  }
}

.cards-title {
  margin-bottom: 4px;
  color: $dark-color;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px; /* 112.5% */
  letter-spacing: -0.64px;
  text-transform: uppercase;
  cursor: pointer;
}

.cards-author {
  color: $secondery-text-color;
  font-family: DM Sans;
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 14px; /* 116.667% */
  letter-spacing: -0.48px;
}

.card-wrapper {
  border: none;
  width: 100%;
  position: relative;
  overflow: hidden;
  transition: transform $anim-tranz-parameters;
}
.card-wrapper:hover {
  transform: scale(1.03);
}

.card-wrapper:hover .hover-text-card {
  visibility: visible;
}

.hover-text-card {
  width: 100%;
  color: $dark-color;
  text-align: center;
  text-transform: uppercase;
  visibility: hidden;
  background-color: $select-el-color;
  border-radius: 0 0 8px 8px;
  padding: 26px;
  font-size: 14px;
  font-weight: 500;
  line-height: 1.29;
  z-index: 9;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translate(-50%);
  pointer-events: none;
}
