.registration-backdrop {
    position: fixed;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(79, 46, 232, 1);
    z-index: 101;
}
  
.registration-modal {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: calc(100% - 20px);
    max-width: 579px;
    transform: translate(calc(-50% - 10px), -50%) scale(1);
    padding: 80px 40px 40px;
    border: 2px solid $dark-color;
    background-color: #F6F6F6;
    border-radius: 18px;
}
.registration-close-icon{
    pointer-events: none;
}

.registration-close-btn {
    position: absolute;
    top: 24px;
    right: 24px;
    width: 14px;
    height: 14px;
    margin-left: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    background: transparent;
    cursor: pointer;
    stroke: $dark-color;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: stroke 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover {
        stroke: $accent-color;
    }
}

.registration-modal-field + .registration-modal-field {
    margin-top: 28px;
}

.registration-modal-field-wrapper {
    position: relative;
    top: 0;
    left: 0;  
    display: flex;
    align-items: center;
}

.registration-modal-icon {
    position: absolute;
    right: 32px;
    top: 50%;
    transform: translateY(-50%);
    transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
    stroke: $dark-color;
    // stroke: #fff;
  }  

.registration-modal-input {
    width: 100%;
    height: 64px;
    padding: 18px 32px;
    border: 2px solid $dark-color;
    border-radius: 40px;
    color: $dark-color;
    font-size: 18px;
    outline: none;
    background-color: transparent;
    transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &::placeholder {
        text-transform: uppercase;
        color: $dark-color;
        font-weight: 700;
        font-size: 18px;
        line-height: 24px;
    }

    &:focus {
        border-color: $accent-color;
    }
}

.signup-btn {
    border: none;
    text-align: center;
    text-transform: uppercase;
    color:#F3F3F3;
    padding: 20px;
    width: 100%;
    margin-top: 40px;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    background-color: $dark-color;
    letter-spacing: -1px; 
    border-radius: 40px;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1)
}

.signup-btn:hover{
    background-color: $accent-color;
}

.registration-inner {
    margin-top: 20px;
    text-align: center;
    display: flex;
    justify-content: space-around;
    
}
.sing-link{
  font-weight: 600;
  transition: color $anim-tranz-parameters;
  &:hover{
    color: $accent-color;
  }  
}

.registration-link {
    text-transform: uppercase;
    font-weight: 700;
    font-size: 18px;
    line-height: 24px;
    color: rgba(17, 17, 17, 0.5);
    transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

    &:hover {
        color: $accent-color;
        text-decoration: underline;
    }
} 

.registration-link + .registration-link {
    margin-left: 20px;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
    .registration-modal {
        height: 384px;
        padding: 40px 20px;
    }
    .registration-close-btn {
        top: 14px;
        right: 14px;
        font-size: 16px;
    }
    .registration-modal-input{
        height: 50px;
    }

    .registration-modal-field + .registration-modal-field {
        margin-top: 24px;
    }
    .signup-btn {
        padding: 12px;
        font-size: 16px;
        height: 50px;
        line-height: 18px;
        margin-top: 24px;
    }

    .registration-modal-icon  {
        width: 18px;
        height: 18px;
    }

    .registration-modal-input::placeholder {
        font-size: 16px;
        line-height: 18px;
    }

    .registration-inner {
        margin-top: 14px;
    }

    .registration-link {
        font-size: 14px;
    }
  }

.dark {
    .registration-modal {
        background-color: rgba(32, 32, 36, 1);
        color: rgba(246, 246, 246, 1);
        border-color: rgba(246, 246, 246, 1);
    }

    .registration-close-btn,
    .registration-modal-icon  {
        stroke:rgba(246, 246, 246, 1);
        transition: stroke 250ms cubic-bezier(0.4, 0, 0.2, 1);

        &:hover {
        stroke: $accent-color;
    }
    }

    .registration-modal-input {
        border-color: rgba(246, 246, 246, 1);
        color: rgba(246, 246, 246, 1);
        transition: border-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
        &:focus {
            border-color: $accent-color;
        }
    }

    .registration-modal-input::placeholder {
        color: rgba(246, 246, 246, 1);
    }

    .signup-btn {
        background-color: rgba(246, 246, 246, 1);
        color: $dark-color;
        transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1), color 250ms cubic-bezier(0.4, 0, 0.2, 1);

        &:hover {
            background-color: $accent-color;
            color: rgba(246, 246, 246, 1);
        }
    }

    .registration-link {
        color: rgba(246, 246, 246, 0.5);
        transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

        &:hover {
        color: $accent-color;
        text-decoration: underline;
    }
    }
    .sing-link{
        color:$light-color;
    }
    
}



